import React, { Component } from "react";
import Axios from "axios";
import FormTerimaSupplier from "./Component/FormTerimaSupplier";
import { getUUID } from "../actions/fakturAction";
import { connect } from "react-redux";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import { reset } from "redux-form";
import Swal from "sweetalert2";
import {
  getReturnSupplierTemp,
  setTotalNW,
  setTotalOngkir,
} from "../actions/terimaSupplierAction";
import { getsupplierList } from "../actions/supplierAction";
import EditComponent from "../Stocking/Component/EditComponent";
import PrintNota from "../Stocking/Component/PrintNota";
import { warning } from "../Alert";
// import { Link } from "react-router-dom";
import { deleteIndex } from "../deleteLocalStorage";
import ReactTooltip from "react-tooltip";
import { getListReturnSupplier } from "../actions/returnSupplier";
import {
  getBruto,
  getNetto,
  getPkg,
  getQty,
  getRp,
} from "../actions/LogicAction";

const server = process.env.REACT_APP_BACKEND_URL;
const serverPOST = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
const maptostate = (state) => {
  return {
    supplierList: state.provinsi.supplier,
    tampungBarang: state.provinsi.returnSupplier,
    noFaktur: state.provinsi.noFaktur,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    username: state.provinsi.username,
    sumBruto: state.perhitungan.sumBruto,
    sumNetto: state.perhitungan.sumNetto,
    sumRp: state.perhitungan.sumRp,
    sumPkg: state.perhitungan.sumPkg,
    sumQty: state.perhitungan.sumQty,
  };
};
class ReturnSupplier extends Component {
  constructor(props) {
    var date = new Date();
    var formatedDate = `${date.getFullYear()}-${
      date.getMonth() + 1
    }-${date.getDate()}`;
    super(props);
    this.state = {
      listSupplier: [],
      DataSupplier: [],
      kodeSupplier: "",
      namaSupplier: "",
      tanggal: formatedDate,
      noReturn: "",
      noBon: "",
      status: "",
      totalNW: 0,
      totalOngkir: 0,
      dateStart: formatedDate,
      tampungBarang: [],
      tampungBarangLocal: [],
      columns: [
        {
          dataField: "kodeJenis",
          text: "Kode Barang",
        },
        {
          dataField: "kadarCetak",
          text: "Kadar Cetak(%)",
        },
        {
          dataField: "p",
          text: "P",
        },
        {
          dataField: "k",
          text: "K",
        },
        {
          dataField: "pkg",
          text: "Bungkus(Gr)",
        },
        {
          dataField: "bruto",
          text: "Berat(Gr)",
        },
        {
          dataField: "kadarTransaksi",
          text: "Harga(%)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
          formatter: (cell) => (
            <span>Rp. {parseFloat(cell).toLocaleString("ID-id")}</span>
          ),
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger mr-2"
                  onClick={() =>
                    deleteIndex(
                      "ReturnSupplier",
                      rowIndex,
                      this.props.dispatch,
                      getListReturnSupplier()
                    )
                  }
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash mr-1"></i>
                </button>
                <br />
                {/* <button
                  data-tip
                  data-for="edit"
                  className="btn btn-warning mr-2"
                  data-toggle="modal"
                  data-target="#editModal"
                  onClick={() => this.setEdit(row)}
                >
                  <ReactTooltip id="edit" type="dark" effect="solid">
                    <span>Edit</span>
                  </ReactTooltip>
                  <i className="fa fa-edit mr-1"></i>
                </button> */}
              </div>
            );
          },
        },
      ],
      urutan: localStorage.getItem("urutan") || 0,
    };
  }

  componentDidMount() {
    this.props.dispatch(getsupplierList());
    Axios.get(server + "suppliers")
      .then((res) =>
        this.setState({
          listSupplier: res.data,
        })
      )
      .catch((err) => alert(err));
    this.props.dispatch(getUUID());
    this.props.dispatch(getListReturnSupplier());
    this.props.dispatch(setTotalOngkir());
    this.props.dispatch(setTotalNW());
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  pilihSupplier = (input) => (e) => {
    var supp = e.target.value ? e.target.value : "DEFALT | DEFAULT";
    var result_explode = supp.split("|");
    this.setState({
      kodeSupplier: result_explode[0],
      namaSupplier: result_explode[1],
    });
  };

  handleSubmit(data) {
    var jenis = data.kodeJenis ? data.kodeJenis : "DEFALT | DEFAULT";
    var result_explode = jenis.split("|");
    let simpan = {
      noFaktur: this.props.noFaktur.toUpperCase(), //
      nettoDiscGlobalExt: 0,
      gross: parseFloat(data.gross), //berat + bungkus
      netto: parseFloat(data.netto), //murni
      qty: 0,
      k: parseFloat(data.k),
      rp: data.rp === undefined ? 0 : parseFloat(data.rp), //ongkos
      kadarCetak: result_explode[2],
      keterangan: data.keterangan.toUpperCase(),
      discGlobalPr: 0,
      kodeKategori: "-",
      kadarTransaksi: parseFloat(data.kadarTransaksi), //harga%
      kodeJenis: result_explode[0], //kodebarang
      noBarcode: "-",
      p: parseFloat(data.p),
      discGlobalGr: 0,
      pkg: parseFloat(data.pkg),
      bruto: parseFloat(data.bruto), //berat
      nettoDiscGlobal: 0,
    };
    let array = JSON.parse(localStorage.getItem("ReturnSupplier")) || [];
    array.push(simpan);
    localStorage.setItem("ReturnSupplier", JSON.stringify(array));
    this.props.dispatch(getListReturnSupplier());
    Swal.fire({
      position: "top-end",
      icon: "success",
      text: "Data Berhasil Ditambahkan!",
      showConfirmButton: false,
      timer: 1500,
    })
      .then(() => this.props.dispatch(reset("TerimaSupplier")))
      .then(() => this.props.dispatch(getReturnSupplierTemp()))
      .then(() => this.props.dispatch(setTotalNW()))
      .then(() => this.props.dispatch(setTotalOngkir()))
      .then(() => document.getElementById("dismiss_modal").click());
  }
  getSumData() {
    this.props.dispatch(getNetto("ReturnSupplier"));
    this.props.dispatch(getBruto("ReturnSupplier"));
    this.props.dispatch(getRp("ReturnSupplier"));
    this.props.dispatch(getPkg("ReturnSupplier"));
    this.props.dispatch(getQty("ReturnSupplier"));
  }

  kirimData() {
    var supplier = this.state.kodeSupplier
      ? this.state.kodeSupplier
      : "DEFAULT|DEFUALT";
    var explode = supplier.split("|");
    if (localStorage.getItem("ReturnSupplier") === null) {
      warning("Barang masih kosong, Mohon tambahkan barang");
      return false;
    } else if (this.state.noBon === "") {
      warning("Mohon isi nomor bon");
      return false;
    } else if (this.state.noReturn === "") {
      warning("Mohon isi nomor Return");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Mohon isi tanggal");
      return false;
    } else if (this.state.kodeSupplier === "") {
      warning("Mohon pilih supplier");
      return false;
    }
    var simpan = {
      footerItem: {
        totalNettoExt: 0,
        totalBruto: this.props.sumBruto,
        totalNetto: this.props.sumNetto,
        totalQty: 0,
        totalP: 0,
        totalK: 0,
        totalpkg: this.props.sumPkg,
        totalGross: 0,
        totalRp: this.props.sumRp,
      },
      footerDocument: {
        inputBy: this.props.username,
      },
      barangDetails: JSON.parse(localStorage.getItem("ReturnSupplier")),
      noFaktur: this.props.noFaktur.toUpperCase(),
      tanggal: this.state.tanggal,
      noBon: this.state.noBon.toUpperCase(),
      kodeSupplier: explode[0],
      namaSupplier: explode[1],
    };
    console.log(JSON.stringify(simpan));
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("ReturnSupplier"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kodeJenis,
        ticket.kadarCetak,
        ticket.p,
        ticket.k,
        ticket.pkg,
        ticket.bruto,
        ticket.kadarTransaksi,
        ticket.netto,
        parseFloat(ticket.rp).toLocaleString("id-ID"),
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      "",
      "",
      table.map((list) => list.pkg).reduce((a, b) => a + b, 0),
      table.map((list) => list.bruto).reduce((a, b) => a + b, 0),
      "",
      table
        .map((list) => list.netto)
        .reduce((a, b) => a + b, 0)
        .toFixed(3),
      table
        .map((list) => list.rp)
        .reduce((a, b) => a + b, 0)
        .toLocaleString("id-ID"),
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      "No",
      "Kode Barang",
      "Kadar Cetak\n(%)",
      "P",
      "K",
      "Bungkus\n(Gr)",
      "Berat\n(Gr)",
      "Harga\n(Gr)",
      "Murni\n(Gr)",
      "Ongkos\n(Rp)",
    ];
    // AKHIR INISIALISASI AUTOTABLE
    Axios.post(serverPOST + "/spp/dc/rtr", simpan)
      .then((res) =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() =>
            PrintNota(
              "no Return",
              this.state.noReturn.toUpperCase(),
              "Tanggal",
              this.state.tanggal,
              "No Bon",
              this.state.noBon.toUpperCase(),
              "Supplier",
              this.state.kodeSupplier.toUpperCase(),
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "Return Barang Supplier",
              tableRows
            )
          )
          .then(() => localStorage.clear())
          .then(() => this.props.dispatch(getReturnSupplierTemp()))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => localStorage.setItem("isLogin", true))
          .then(() => this.props.dispatch(getListReturnSupplier()))
          .then(() =>
            this.setState({
              noBon: "",
              noReturn: "",
            })
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: "Data Gagal Ditambahkan",
          showConfirmButton: false,
          timer: 1500,
        })
      );
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-9">
                      <h1>Return Barang Supplier</h1>
                    </div>
                    {/* <div className="col-lg-3">
                      <div className="text-right">
                        <Link to="/ayugold.com/EditForm/returnSupplier">
                          <button
                            className="btn btn-warning"
                            // data-target="#editModal"
                            // data-toggle="modal"
                          >
                            Edit Return Barang
                          </button>
                        </Link>
                      </div>
                    </div> */}
                    <div className="col-lg-12">
                      <br />
                    </div>
                    <div className="col-lg-4 col-md-4">
                      <label htmlFor="">Supplier</label>
                      <select
                        name="supplier"
                        id="supplier"
                        className="form-control"
                        defaultValue="DEFAULT | DEFAULT"
                        onChange={this.pilihSupplier()}
                      >
                        <option value="DEFAULT | DEFAULT" key="DEFAULT">
                          Silahkan Pilih
                        </option>
                        {this.state.listSupplier.map((list) => (
                          <option
                            value={list.kodeSupplier + "|" + list.namaSupplier}
                          >
                            {list.namaSupplier}
                          </option>
                        ))}
                      </select>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Tanggal</label>
                      <input
                        type="date"
                        id="tanggal"
                        name="tanggal"
                        defaultValue={this.state.dateStart}
                        className="form-control"
                        onChange={this.inputChange("tanggalInvoice")}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">No Return</label>
                      <input
                        type="text"
                        id="no_return"
                        name="no_return"
                        className="form-control"
                        onChange={this.inputChange("noReturn")}
                        value={this.state.noReturn}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4 mt-3">
                      <label htmlFor="">No Bon</label>
                      <input
                        type="text"
                        id="bon_return"
                        name="bon_return"
                        className="form-control"
                        onChange={this.inputChange("noBon")}
                        value={this.state.noBon}
                      />
                    </div>
                    <div className="col-md-12 col-lg-12 mt-3 mb-3">
                      <div className="row">
                        <div className="col-lg-6 col-md-6">
                         
                        </div>
                        <div className="col-lg-6 col-md-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              data-toggle="modal"
                              data-target="#primary-header-modal"
                            >
                              Tambah Data
                              <i class="fa fa-plus ml-2" aria-hidden="true"></i>
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                  {this.props.tampungBarang ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.props.tampungBarang}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <ToolkitProvider
                      keyField="id"
                      data={[]}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                            noDataIndication="Tidak Ada Data"
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  )}
                   <div className="text-right">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.kirimData()}
                            >
                              Simpan
                              <i class="fa fa-paper-plane ml-2" aria-hidden="true"></i>
                            </button>
                          </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Return Supplier
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormTerimaSupplier
                  from="return"
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
        <div
          id="editModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Return Supplier
                </h4>
                <button
                  type="button"
                  id="dismiss_modal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <EditComponent
                  from="return"
                  onSubmit={(data) => this.handleSubmit(data)}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptostate, null)(ReturnSupplier);
